import React, { Component } from 'react'
import { Chip, Typography, Button, Box, Avatar, Icon, Grid, Grid2 } from '@mui/material';

import FaceIcon from '@mui/icons-material/Face';
import { motion } from 'framer-motion';

const animation = {
    translateX: [0, 100, 100, 0, 0],
    translateY: [0, 0, 0, 100, 100],
    transition: {
    repeat: Infinity,
    repeatType: "reverse"
    }
}


export class pg1 extends Component {
    render() {
        return (
            <div>

                <Box className="pg-1" >


                    <Box className="header-wrapper"
                    >
                        <Grid container spacing={0} className="header-grid">

                            <Grid xs={2} className="header-name-box"
                            component={motion.div}
                            initial={{ y: "-3vw", opacity: "60%" }}
                            whileInView={{ opacity: "100%", y: 0 }}
                            transition={{ 
                                delay: 0, 
                                duration: 0.8,}}>
                            <Chip icon={<FaceIcon />} label="Mongo DB" variant="filled" color="primary" />
                            </Grid>

                            <Grid 
                            xs={4} className="header-spacing">
                            </Grid>

                            <Grid xs={4} className="header-button-box"
                            component={motion.div}
                            initial={{ y: "-3vw", opacity: "60%" }}
                            whileInView={{ opacity: "100%", y: 0 }}
                            transition={{ 
                                delay: 0, 
                                duration: 1.2,}}>
                                <Button variant="text" color="secondary">About</Button>
                                <Button variant="text" color="secondary">Projects</Button>
                                <Button variant="text" color="secondary">Contact</Button>
                            </Grid>

                            <Grid xs={2} className="header-icon-box" sx={{ paddingLeft: "10%"}}
                            component={motion.div}
                            initial={{ y: "-3vw", opacity: "60%" }}
                            whileInView={{ opacity: "100%", y: 0 }}
                            transition={{ 
                                delay: 0, 
                                duration: 1.6,}}>
                            <Avatar  sx={{ bgcolor: "#000", width: "4vw", height: "4vw"}}>N</Avatar>
                            </Grid>

                        </Grid>
                    </Box>


                    <Box className="name-wrapper" >
                        <Grid container spacing={0} className="name-grid">

                            <Grid xs={12} className="nametxt-box"
                             component={motion.div}
                             initial={{ x: "-45vw", opacity: "60%" }}
                             whileInView={{ opacity: "100%", x: 0 }}
                             transition={{ 
                                 delay: 0.4, 
                                 duration: 1,}}>
                                <Typography variant="h1" className="text-1" >
                                    Elijah Allotey
                                </Typography>

                            </Grid>

                            <Grid xs={12} className="flvtext-box"
                            component={motion.div}
                            initial={{ x: "-45vw", opacity: "60%" }}
                            whileInView={{ opacity: "100%", x: 0 }}
                            transition={{ 
                                delay: 0.6, 
                                duration: 1,}}>
                                <Typography variant="h2" className="text-2" >
                                    A Web Developer with a passion for creating and designing websites and applications.
                                </Typography>
                            </Grid>

                        </Grid>
                    </Box>
                    

                    <Box className="screen-wrapper" 
                    component={motion.div}
                    initial={{scale: 0.5, opacity: "60%" }}
                    whileInView={{ scale: 1, opacity: "100%" }}
                    transition={{ 
                        delay: 0.2, 
                        duration: 0.8,}}>
                        <Box className="screen1-box">
                        </Box>
                        <Box className="screen2-box">
                            <Grid container spacing={0} className="screen-grid">
                                <Grid item xs={12} className="screen-text-box">
                            <Typography variant="h1" className="text-3" >
                                What im working on
                            </Typography>
                            </Grid>

                            <Grid item xs={12} className="screen-text-box">
                            <Typography variant="h2" className="text-3" >
                                With my portfolio website at a level of completion, I am now working on a website for a local business.
                            </Typography>
                            </Grid>

                            
                            <Grid item xs={12} className="screen-text-box">
                            
                            <Button variant="contained" color="secondary" className="proj-btn">View Project</Button>
                            </Grid>
                            
                            </Grid>

                        </Box>
                    
                    </Box>

                    {/* <Box className="tech-wrapper" >
                        <Grid container spacing={0} className="tech-grid">
                            
                            <Typography variant="h6" className="tech-title" >
                                Frequent Tech
                            </Typography>

                            <Grid xs={6} className="tech-chip1">
                            <Chip icon={<FaceIcon />} label="React JS" variant="outlined" color="secondary" />
                            </Grid>

                            <Grid xs={6} className="tech-chip2">
                            <Chip icon={<FaceIcon />} label="VS Code" variant="outlined" color="secondary" />
                            </Grid>

                            <Grid xs={6} className="tech-chip3">
                            <Chip icon={<FaceIcon />} label="Git/Github" variant="outlined" color="secondary" />
                            </Grid>

                            <Grid xs={6} className="tech-chip4">
                            <Chip icon={<FaceIcon />} label="Mongo DB" variant="outlined" color="secondary" />
                            </Grid>

                        </Grid>
                    </Box> */}

                    <Box className="anim-wrapper" >
                        <Grid 
                        container 
                        spacing={0}
                        justifyContent="center" 
                        className="anim-grid"
                        >

                            

        
    
                                <Grid xs={10} 
                                className="anim-box1"
                                component={motion.div}
                                initial={{ y: 5, opacity: "60%" }}
                                whileInView={{ opacity: "100%", y: 30 }}
                                transition={{ 
                                    delay: 0, 
                                    duration: 1, 
                                    repeat: Infinity, 
                                    repeatType: "reverse" }}>

                                        text
                                </Grid>

                                <Grid xs={9} 
                                className="anim-box2"
                                component={motion.div}
                                initial={{ y: 5, opacity: "60%" }}
                                whileInView={{ opacity: "100%", y: 32 }}
                                transition={{ 
                                    delay: 0.5, 
                                    duration: 1, 
                                    repeat: Infinity, 
                                    repeatType: "reverse" }}>

                                        text
                                </Grid>

                                <Grid xs={8} 
                                className="anim-box3"
                                component={motion.div}
                                initial={{ y: 5, opacity: "60%" }}
                                whileInView={{ opacity: "100%", y: 32 }}
                                transition={{ 
                                    delay: 1, 
                                    duration: 1, 
                                    repeat: Infinity, 
                                    repeatType: "reverse" }}>

                                        text
                                </Grid>

                                <Grid xs={7} 
                                className="anim-box4"
                                component={motion.div}
                                initial={{ y: 5, opacity: "60%" }}
                                whileInView={{ opacity: "100%", y: 32 }}
                                transition={{ 
                                    delay: 1.5, 
                                    duration: 1, 
                                    repeat: Infinity, 
                                    repeatType: "reverse" }}>

                                        text
                                </Grid>

                                <Grid xs={6} 
                                className="anim-box5"
                                component={motion.div}
                                initial={{ y: 5, opacity: "60%" }}
                                whileInView={{ opacity: "100%", y: 32 }}
                                transition={{ 
                                    delay: 2, 
                                    duration: 1, 
                                    repeat: Infinity, 
                                    repeatType: "reverse" }}>

                                        text
                                </Grid>
                            

                            

                            
                            </Grid>
                        
                    
                    </Box>

                    <Box className="title-wrapper"
                    component={motion.div}
                    initial={{ y: 0, opacity: "60%" }}
                    whileInView={{ opacity: "100%", y: -32 }}
                    transition={{ 
                        delay: 0, 
                        duration: 1, 
                        repeat: Infinity, 
                        repeatType: "mirror",
                        repeatDelay: 1 }}>
                        <Typography variant="h3" className="text-2" >
                            Web Developer
                        </Typography>
                    </Box>


                </Box>
                {/* wrapper of page1 ends here  */}



            </div>
        )
    }
}

export default pg1;

