import React, { Component } from 'react';
import { Typography, Button, Box, Grid, Grid2, TextField} from '@mui/material';

import FaceIcon from '@mui/icons-material/Face';
import { motion } from 'framer-motion';
import { useState } from 'react';

const Pg7 = () => {

    const [inputs, setInputs] = useState({
      firstname: "",
      lastname: "",
      email: "",
      message: "",
    });
   
    
    // These methods will update the state properties.
    function updateInputs(value) {
      return setInputs((prev) => {
        return { ...prev, ...value };
      });
    }
    
    // This function will handle the submission.
    async function onSubmit(e) {
      e.preventDefault();
    
      // When a post request is sent to the create url, we'll add a new record to the database.
      const newPerson = { ...inputs };
      console.log(newPerson);
    
      await fetch("http://localhost:5000/record/add", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newPerson),
        
      })
      .catch(error => {
        window.alert(error);
        return;
      });
    
      setInputs({ firstname: "", lastname: "", email: "", message: "" });
      
    }


    return (
      <div>
        <Box className="pg-7">

            <Box className="header7-wrapper">
            </Box>

            <Box className="contact-wrapper"
            component={motion.div}
            initial={{ opacity: "60%", scale: 0.8 }}
            whileInView={{ opacity: "100%", scale: 1 }}
            transition={{ 
                delay: 0, 
                duration: 0.8,}}>
                <Grid container spacing={0} className="contact-grid">
                <form onSubmit={onSubmit}>
                <Grid className="location" container spacing={1}>
                    <Grid xs={12} className="title-box">
                        <Typography variant="h1" className="text-1" >
                            Contact Me
                        </Typography>
                    </Grid>
                                <Grid xs={12} sm={6} item
                                component={motion.div}
                                initial={{ y: "2vh", opacity: "60%" }}
                                whileInView={{ opacity: "100%", y: 0 }}
                                transition={{ 
                                    delay: 0, 
                                    duration: 0.8,}}>



                                    <TextField 
                                    value={inputs.firstname}
                                    onChange={(e) => updateInputs({ firstname: e.target.value })}
                                    name="firstName"
                                    type="text"
                                    label="First Name"
                                    placeholer="Enter first name" 
                                    variant="outlined" 
                                    fullWidth 
                                    required sx={{ label: { color: "#fff" }, input: {color: "#fff"} }} />
                                </Grid>




                                <Grid xs={12} sm={6} item
                                component={motion.div}
                                initial={{ y: "2vh", opacity: "60%" }}
                                whileInView={{ opacity: "100%", y: 0 }}
                                transition={{ 
                                    delay: 0.1, 
                                    duration: 0.8,}}>




                                    <TextField 
                                    type="text"
                                    name="lastName"
                                    value={inputs.lastname}
                                    onChange={(e) => updateInputs({ lastname: e.target.value })}
                                    label="Last Name" 
                                    placeholer="Enter Last name" 
                                    variant="outlined" 
                                    fullWidth required 
                                    sx={{ label: { color: "#fff" }, 
                                    input: {color: "#fff"} }} />
                                </Grid>




                                <Grid xs={12} item
                                component={motion.div}
                                initial={{ y: "2vh", opacity: "60%" }}
                                whileInView={{ opacity: "100%", y: 0 }}
                                transition={{ 
                                    delay: 0.2, 
                                    duration: 0.8,}}>




                                    <TextField 
                                    type="email"
                                    name="email"
                                    value={inputs.email}
                                    onChange={(e) => updateInputs({ email: e.target.value })}
                                    label="Email" 
                                    placeholer="Enter email" 
                                    variant="outlined" 
                                    fullWidth 
                                    required 
                                    sx={{ label: { color: "#fff" }, input: {color: "#fff"} }} />
                                </Grid>
                                
                                <Grid xs={12} item
                                component={motion.div}
                                initial={{ y: "2vh", opacity: "60%" }}
                                whileInView={{ opacity: "100%", y: 0 }}
                                transition={{ 
                                    delay: 0.4, 
                                    duration: 0.8,}}>




                                    <TextField 
                                    type="text"
                                    name="message"
                                    value={inputs.message}
                                    multiline rows={6} 
                                    onChange={(e) => updateInputs({ message: e.target.value })}
                                    label="Message" 
                                    placeholer="Type your message here" 
                                    sx={{ label: { color: "#fff" }, input: {color: "#fff"} }} 
                                    variant="outlined" 
                                    fullWidth 
                                    required 
                                    inputProps={{ style: { color: "white" } }} />
                                    
                                    
                                     
                                </Grid>
                                
                                
                                
                                <Grid xs={12} item>
                                    <Button 
                                    type="submit" 
                                    variant="contained" 
                                    color="secondary">Submit</Button>
                                </Grid>
                            </Grid>
                            </form>
                </Grid>
            </Box>

            <Box className="final-wrapper"
            component={motion.div}
            initial={{ opacity: "60%", scale: 0.8 }}
            whileInView={{ opacity: "100%", scale: 1 }}
            transition={{ 
                delay: 0, 
                duration: 0.8,}}>
                <Grid container spacing={0} className="final-grid">
                    
                    <Grid xs={12} item
                    component={motion.div}
                    initial={{ y: "2vh", opacity: "60%" }}
                    whileInView={{ opacity: "100%", y: 0 }}
                    transition={{ 
                        delay: 0.2, 
                        duration: 0.8,}}>
                        <Typography variant="h1" className="text-1" >
                            Got a project in mind?
                        </Typography>
                    </Grid>
                    <Grid xs={12} item
                    component={motion.div}
                    initial={{ y: "2vh", opacity: "60%" }}
                    whileInView={{ opacity: "100%", y: 0 }}
                    transition={{ 
                        delay: 0.3, 
                        duration: 0.8,}}>
                        <Typography variant="h2" className="text-2" >
                            Im open to new opportunities. Im especially
                            interested in working with startups, small 
                            businesses, personal projects etc. , so if 
                            you have a project in mind, feel free to contact me.
                        </Typography>
                    </Grid>
                    <Grid xs={12} item
                    component={motion.div}
                    initial={{ y: "5vh", opacity: "60%", x: "2vw" }}
                    whileInView={{ opacity: "100%", y: "3vh" }}
                    transition={{ 
                        delay: 0, 
                        duration: 0.8,}}>
                        <Button className="top-button" variant="contained" color="secondary" href="#top">Back to top</Button>
                    </Grid>
                </Grid>

            </Box>

            <Box className="resume-wrapper"
            component={motion.div}
            initial={{ opacity: "60%", scale: 0.8 }}
            whileInView={{ opacity: "100%", scale: 1 }}
            transition={{ 
                delay: 0, 
                duration: 0.8,}}>
                <Grid container spacing={0} className="resume-grid">
                    <Grid xs={12} item
                    component={motion.div}
                    initial={{ y: "2vh", opacity: "60%", x: "2vw" }}
                    whileInView={{ opacity: "100%", y: "0vh" }}
                    transition={{ 
                        delay: 0, 
                        duration: 0.8,}}>
                        <Typography variant="h1" className="text-1" >
                            My resume
                        </Typography>
                    </Grid>
                    <Grid xs={12} item
                    component={motion.div}
                    initial={{ y: "2vh", opacity: "60%", x: "4vw" }}
                    whileInView={{ opacity: "100%", y: "0vh" }}
                    transition={{ 
                        delay: 0, 
                        duration: 0.8,}}>
                        <Button className="top-button" variant="contained" color="secondary" href="#top">Back to top</Button>
                    </Grid>
                    
                </Grid>
            </Box>

            

        </Box>
      </div>
    )
  }


export default Pg7

